<template>
  <div class="mine">
  	<div class="zz"></div>
  	<header>
  	<span v-text="_Position"></span>
  	<div>
  		
  		<input type="text" placeholder="2" />
  		<a href="#"><img src="//image.benlailife.com/static/images/mycenter/login_n_ico05_0e143fe9.png"></a>
