  			<a @click="change('','danpin')" :class="{active:active==''?true:false}" :names="{active}" ><span :class="{active:active==''?true:false}"></span></a>
  			<a @click="change('','haixian')" :class="{active:active==''?true:false}" :names="{active}" ><span :class="{active:active==''?true:false}"></span></a>
  			<a @click="change('','miandian')" :class="{active:active==''?true:false}" :names="{active}" ><span :class="{active:active==''?true:false}"></span></a>
  			<a @click="change('','fushi')" :class="{active:active==''?true:false}" :names="{active}" ><span :class="{active:active==''?true:false}"></span></a>
  			<a @click="change('','rupin')" :class="{active:active==''?true:false}" :names="{active}" ><span :class="{active:active==''?true:false}"></span></a>
  			<a @click="change('','shipin')" :class="{active:active==''?true:false}" :names="{active}" ><span :class="{active:active==''?true:false}"></span></a>
  			<a @click="change('','chayin')" :class="{active:active==''?true:false}" :names="{active}" ><span :class="{active:active==''?true:false}"></span></a>
  			<a @click="change('','yongpin')" :class="{active:active==''?true:false}" :names="{active}" ><span :class="{active:active==''?true:false}"></span></a>
  			
  		</div>
  		<class-right :active='active' :num='num'></class-right>
  	</div>
  	<foo-nav></foo-nav>
  </div>
</template>

<script>
import Classright from './ClassComponents/Classright'
import Footers from './Footers'
import {mapGetters} from "vuex"
export default {
  name: 'classify',
  data () {
  	
    return {
    	active:'',
    	num:'zhendian'
    }
  },
  computed:{
  	...mapGetters(["_Position"])
  },
  methods:{
  	
  	change(data,i){
  		this.active=data
  		this.num=i
  	//console.log(this.active)
  	}
  },
  components:{
  	'class-right':Classright
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
