 .mine{
 	width: 100%;
 	height: 100%;
 	.zz{
 		width: 100%;
 		height: 50px;
 	}
 
 		header{
 			width: 100%;
 			display:flex;
 			position: fixed;
 			z-index: 999;
 			background-color: #FFFFFF;
 			top:0;
 			
 		
 			
 			span{
 				margin: 0.5rem;
		 			display: inline-block;
		 			padding: 0.5rem 1rem;
		 		
		 		}
		 	div{
		 		margin: 1rem;
		 		border-radius: 1.5rem;
		 		border: 1px solid #e1e1e1;
		 		flex: 1;
		 		
		 		input{
