		 				right: 0.5rem;
		 			}
		 		}
		 	}
 		}
 		.list-box{
 			width: 100%;
 			height: 100%;
 		}
	.list-left{
		float: left;
		width: 5.6rem;
		position: fixed;
		left: 0;
		a{
			display: inline-block;
			padding: 0.4rem 0;
			border-bottom: 1px solid #F5F5F5;
		
		}
		span{
			display: inline-block;
			margin: 0.7rem 0;
			padding: 0 0.5rem;
			border-left: 2px solid #fff;
			width: 100px;
			text-align: center;
		}
	}
 	span.active{
 			border-left: 3px solid #ABD13E;
 			color: #ABD13E;
 	}
 	a.active{
 		background-color: #f2f2f2;
 	}
 	
 			
 		
 	}
</style>



